<template>
 <div>
        <mu-flexbox class="mt8" orient="vertical">
            <mu-flexbox-item order="2" class="item">
             <div> <span class="title">影院热映</span>  <router-link class="more" to="/hot">更多</router-link></div>
              <mu-circular-progress class="circle" :size="60"  v-show="hotReady"/>
              <div style="text-align:center;" v-show="!hotReady">TMD没有数据</div>
               <mu-row gutter>
                  <mu-col class="list-item" width="50" tablet="25" desktop="25" v-for="item in hotItem" :key="item.id">
                    <router-link :to="{name:'detail',params:{id:item.id}}">
                    <img :src="item.images.large" :alt="item.alt" style="width:100%;height:20rem;">
                    </router-link>
                    <p>{{item.title}}</p>
                    <p>评分：{{item.rating.average}}</p>
                  </mu-col>
                </mu-row>
            </mu-flexbox-item>
            
            <mu-flexbox-item order="2" class="item">
              <div><span class="title">新片速递</span>  <router-link class="more" to="/comingsoon">更多</router-link></div>
              <mu-circular-progress class="circle" :size="60"  v-show="comingsoonReady"/>
              <div style="text-align:center;" v-show="!hotReady">TMD没有数据</div>
             <mu-row gutter>
                  <mu-col class="list-item" width="50" tablet="25" desktop="25" v-for="item in comingsoonItem" :key="item.id">
                    <router-link :to="{name:'detail',params:{id:item.id}}">
                    <img :src="item.images.large" :alt="item.alt" style="width:100%;height:20rem;">
                    </router-link>
                    <p>{{item.title}}</p>
                    <p>评分：{{item.rating.average}}</p>
                  </mu-col>
                </mu-row>
            </mu-flexbox-item>
            <mu-flexbox-item order="2" class="item">
              <div><span class="title">北美票房</span>  <router-link class="more" to="/usbox">更多</router-link></div>
              <mu-circular-progress class="circle" :size="60" v-show="usboxReady"/>
              <div style="text-align:center;" v-show="!hotReady">TMD没有数据</div>
               <mu-row gutter>
                  <mu-col class="list-item" width="50" tablet="25" desktop="25" v-for="item in usboxItem" :key="item.id">
                    <router-link :to="{name:'detail',params:{id:item.id}}">
                    <img :src="item.images.large" :alt="item.alt" style="width:100%;height:20rem;">
                    </router-link>
                    <p>{{item.title}}</p>
                    <p>评分：{{item.rating.average}}</p>
                  </mu-col>
                </mu-row>
            </mu-flexbox-item>
            <mu-flexbox-item order="2" class="item">
              <div><span class="title">经典电影</span>  <router-link class="more" to="/top250">更多</router-link></div>
              <mu-circular-progress class="circle" :size="60" v-show="top250Ready"/>
              <div style="text-align:center;" v-show="!hotReady">TMD没有数据</div>
               <mu-row gutter>
                  <mu-col class="list-item" width="50" tablet="25" desktop="25" v-for="item in top250Item" :key="item.id">
                    <router-link :to="{name:'detail',params:{id:item.id}}">
                    <img :src="item.images.large" :alt="item.alt" style="width:100%;height:20rem;">
                    </router-link>
                    <p>{{item.title}}</p>
                    <p>评分：{{item.rating.average}}</p>
                  </mu-col>
                </mu-row>
            </mu-flexbox-item>
      </mu-flexbox>
       <mu-circular-progress :size="40" v-show="showProgress"/>

 </div>
</template>
<script>
export default {
  name: 'home',
  data () {
    return {
      showProgress:false,
      start:0,
      count:5,
      hotReady:true,
      comingsoonReady:true,
      usboxReady:true,
      top250Ready:true,
      hotItem:[],
      comingsoonItem:[],
      usboxItem:[],
      top250Item:[]
    }
  },
  created () {
        this.getHot();
        this.getComingSoon();
        this.getUsbox();
        this.getTop250();

  },
  methods: {
      getHot:function(){
               this.$http.jsonp(this.$api.hot)
                        .then(function(res){
                            let data = res.data.subjects;
                            this.hotItem = data;
                           if(this.hotItem.length != 0){
                              this.hotReady = false;
                            console.info('hot',  this.hotItem);
                           }
                        },function(){
                              this.hotReady = false;
                              console.log('error')
                        })
      },
      getComingSoon:function(){
               this.$http.jsonp(this.$api.comingsoon)
                        .then(function(res){
                          let data = res.data.subjects;
                           this.comingsoonItem = data;
                            if(this.comingsoonItem.length != 0){
                                  this.comingsoonReady = false ;
                                console.info('comingsoonReady',this.comingsoonItem)
                            }
                        },function(){
                             this.comingsoonReady = false;
                              console.log('error')
                        })
      },
      getUsbox:function(){
               this.$http.jsonp(this.$api.usbox)
                        .then(function(res){
                          let data = res.data.subjects;
                           this.usboxItem = data;
                           
                           if(this.usboxItem.length!= 0 ){
                              this.usboxReady = false;
                              console.log('usboxReady', this.usboxItem)
                           }
                           
                        },function(){
                             this.usboxReady = false;
                              console.log('error')
                        })
      },
      getTop250:function(){
               this.$http.jsonp(this.$api.top250)
                        .then(function(res){
                          let data = res.data.subjects;
                           this.top250Item = data;
                          
                           if(this.top250Item.length != 0){
                              this.top250Ready = false;
                               console.info('top250Ready',this.top250Item)
                           }
                          
                        },function(){
                             this.top250Ready = false;
                                console.log('error')
                        })
      }
  }
}
</script>
<style scoped>
  .item{
            position: relative;
        min-height: 18rem;
  }
  .more{
    float:right;
  }
  .title{
    font-size:16px;
    color:#7E57C2;
  }
  .list-item{
    position:relative;
    display:inline-block;
    width:auto;
  
    margin-bottom:10px;
 
  }
 .list-item p{
    line-height: 1;
    margin: 0 0 10px;
  }
  .itemTitle{
    position:absolute;
    bottom:0;
    width:100%;
    line-height:2;
    margin:0;
    padding-left:20px;
  }
  .circle{
        width: 60px;
        height: 60px;
        position: absolute;
        left: 50%;
        margin-left: -30px;
  }
</style>


